import Vue from 'vue'
import VueRouter from 'vue-router'
import routes from './routes'
import {authMiddleware} from "./middlewares";

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

//middlewares
router.beforeEach(authMiddleware)

export default router
